.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  z-index: 1;
  position: fixed;
  width: 100%;
  padding: 12px 4%;
  font-size: 0.9rem;
  background-color: linear-gradient(
    to bottom,
    rgba(74, 74, 74, 0.6),
    rgba(0, 0, 0, 0)
  );
  transition: background-color 0.3s ease-in-out;
  &__logo {
    color: red;
    font-weight: bold;
    font-size: 3rem;
    @media screen and(max-width: 768px) {
      font-size: 1.5rem;
    }
  }
  &__img {
    display: block;
    width: 1.5rem;
    min-height: 1.5rem;
    max-height: 1.5rem;
  }
  a {
    color: #fff;
    text-decoration: none;
  }
  &--black {
    background-color: #000;
  }
  &__burger {
    cursor: pointer;
    color: #fff;
    margin-right: 1rem;
    background-color: none;
    @media screen and(min-width: 768px) {
      display: none;
    }
  }
  &__links {
    margin-left: 20px;
    transition: background-color 0.3s ease-in-out;
    .nav__link {
      text-decoration: none;
      padding: 5px 10px;
    }
    @media screen and(max-width: 767.98px) {
      margin-left: 0;
      visibility: hidden;
      position: absolute;
      top: 110%;
      right: 0;
      .show & {
        display: flex;
        flex-direction: column;
        visibility: visible;
        width: 100%;
        height: calc(100vh - 60px);
        top: 60px;
        background-color: #000;
      }
      .nav__link {
        font-size: 1.2rem;
        padding: 15px 4%;
      }
    }
  }
  &__actions {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex: 1;
    .nav__action {
      margin-left: 10px;
      @media screen and(min-width: 768px) {
        margin-left: 20px;
      }
    }
  }
}
